'use client'
import styles from "../../page.module.css";
import Form from 'react-bootstrap/Form';
import EntryBox from "../../_components/entrybox";
import Submit from "../../_components/submit";
import Select from "../../_components/select";
import { useEffect, useReducer } from "react";
import CheckBox from "../../_components/checkbox";
import { fetch } from "../../_scripts/communications";
import backgroundImage from "../../../public/Green.png"
import ButtonEntry from "@/app/_components/buttonentry";
import Feedback from "@/app/_components/feedback";
import BasicTable from "@/app/_components/basictable";
import FormTitle from "@/app/_components/formtitle";

let submitObject = {};

const initState = {
    'LSManager Account':{
        value:''
    },

    'Submit':{
        Color:'primary',
        Text:'Submit'
    },
    'Select Session':{
        options: [],
        value:0
    },
    'Scheduling Month':{
        value:''
    },
    'Registrations':[{}]
};


const reducer = (state, action)=>{

    let newState = {...state};

    switch (action.type){

        case 'update-Value':{

            newState[action.label].value = action.value;

            newState.Submit.Color = 'primary';
            newState.Submit.Text = 'Submit';

            return newState

            break
        }

        case 'submit-Clicked':{

            newState.Submit.Color = action.color;
            newState.Submit.Text = action.text;

            return newState

            break;
        }

        case 'submit-Error':{

            //Seperate from click for future purposes

            newState.Submit.Color = action.color;
            newState.Submit.Text = action.text;

            return newState

            break;
        }

        case 'submit-Success':{

            //Seperate from click for future purposes

            newState.Submit.Color = action.color;
            newState.Submit.Text = action.text;

            return newState

            break;

        }
        
        case 'load-Options':{

            //console.dir(action.data);

            let courses = action.data.getCourses;

            newState['Select Session'].options = courses;

            newState['Select Session'].value = courses[0].ID;

            return newState

            break;
        }

        case 'load-Report':{

            let students = action.report;

            newState['Registrations'] = students;

            return newState

            break;
        }

         case 'load-SelectBox':{

            let Sessions = action.data.Sessions;

            newState['Select Session'].options = Sessions;

            newState['Select Session'].value = Sessions[0].ID;

            return newState

            break

        }
        

    }
};

export default function EmployeesInRMU(){


    const [state, dispatch] = useReducer(reducer,initState);

        useEffect(function(){

            const loadDropListOptions = async()=>{

                let options = await fetch({method:'GET',endpoint:'Litmos/LoadEmployeeList'});

                if(options.didError){

                    //Do stuff

                    return

                }

                dispatch({type:'load-Report',report:options.data.data})

            }

            loadDropListOptions();
        
            return

        },[]
    );


    return (

        <main className={styles.main} style={{backgroundImage: `url(${backgroundImage.src})`}}>
            
          <Form>
          <FormTitle text='Employees In RMU'></FormTitle>
            <BasicTable
                records={state['Registrations']}
            >
            </BasicTable>
          </Form>
        </main>
    );
}